import { Person, IPerson } from '../+persons/person';
import { Service } from '../+services/service';
import { Analyzes } from '../+analyzes/analyzes';

export interface IPayment {
    _id?: string;
    personId: string;
    specialists: any[];
    referral: string;
    assistant: string[];
    services: Service[];
    analyzes: Analyzes[];
    payment: {
        type: string;
        paid: number;
        inKass: boolean;
    };
    type: string;
    discount: {
        type: string;
        isPercent: boolean;
        number: number;
    };
    status: string;
}
export class Payment implements IPayment{
    _id?: string;
    personId: string;
    specialists: any[];
    referral: string;
    assistant: string[];
    services: Service[];
    analyzes: Analyzes[];
    payment: {
        type: string;
        paid: number;
        inKass: boolean;
    };
    type: string;
    discount: {
        type: string;
        isPercent: boolean;
        number: number;
    };
    status: string;
    person: Person;

    get toPay() {
        let summ = 0;
        this.services.forEach((item) => summ += item.price);
        this.analyzes.forEach((item) => summ += item.price);
        return summ;
    }

    constructor(options?) {
        this.default();
        if(options) {
            this.setParams(options);
        }
    }

    setParams(options: {
        _id?: string,
        personId?: string,
        specialists?: any[],
        referral?: string,
        assistant?: string[],
        services?: Service[],
        analyzes?: Analyzes[],
        payment?: {
            type: string,
            paid: number,
            inKass: boolean,
        },
        type?: string,
        discount?: {
            type: string,
            isPercent: boolean,
            number: number,
        },
        status?: string,
        person?: Person
    }) {
        this._id = options._id || null;
        this.personId = options.personId || null;
        this.specialists = options.specialists || [];
        this.referral = options.referral || null;
        this.assistant = options.assistant || [];
        if(options.services) {
            this.services = options.services.map((item) => new Service(item));
        }
        if(options.analyzes) {
            this.analyzes = options.analyzes.map((item) => new Analyzes(item));
        }
        this.payment = options.payment || {
            type: 'cash',
            paid: 0,
            inKass: false   
        };
    
        this.type = options.type || 'default';
        this.discount = options.discount || {
            type: 'default',
            isPercent: false,
            number: 0   
        };
           
        this.status = options.status || 'open';
        if(options.person) {
            this.person.setParams(options.person);
        }
    }

    default() {
        this._id = null;
        this.personId = null;
        this.specialists = [];
        this.referral = null;
        this.assistant = [];
        this.services = [];
        this.analyzes = [];
        this.payment = {
            type: 'cash',
            paid: null,
            inKass: false   
        };
    
        this.type = 'default';
        this.discount =  {
            type: 'default',
            isPercent: false,
            number: null   
        };
           
        this.status = 'open';
        this.person = new Person();
    }

    isCollect() {
        this.status = 'collected';
    }

    isClose() {
        this.status = 'close';
    }

    isOpen() {
        this.status = 'open';
    }

    getSpecialists() {
        if(this.specialists.length > 0 && this.specialists[0] !== null) {
            return this.specialists.map((item) => [item.person.last_name, item.person.first_name[0] + '.', item.person.second_name[0] + '.'].join(" "));
        }
        return [];
    }
}
