import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { PaginationService } from '../../shared/pagination.service';
import { ToolsService } from '../../shared/tools.service';

import { ServicesListComponent } from '../../+services/_widgets/services-list/services-list.component';
import { ServicesService } from '../../+services/services.service';
import { Service } from '../../+services/service';

import { AnalyzesListComponent } from '../../+analyzes/_widgets/analyzes-list/analyzes-list.component';
import { AnalyzesService } from '../../+analyzes/analyzes.service';
import { Analyzes } from '../../+analyzes/analyzes';

import { Specialist } from '../../+specialists/specialist';

@Component({
  selector: 'app-payment-form-services-select',
  templateUrl: './payment-form-services-select.component.html',
  styleUrls: ['./payment-form-services-select.component.css'],
  providers: [ServicesService, AnalyzesService]
})
export class PaymentFormServicesSelectComponent implements OnInit {
  @ViewChild(ServicesListComponent) servicesList: ServicesListComponent;
  @ViewChild(AnalyzesListComponent) analyzesList: AnalyzesListComponent;

  @Input('specialists') 
    set specialists(specialists: Specialist[]) {
      this._specialists = specialists;
      this.setServicesFilterBySpecialists(specialists);

      if(!!this.specialists) this.reloadServices(this.servicesFilter);
    }

  _specialists: Specialist[];
  get specialists() {
    return this._specialists;
  }
  servicesFilter: {
    variant: string,
    tags: string[],
    search: string
  } = {
    variant: 'default',
    tags: [],
    search: ''
  };

  profiles: any[];

  // Services
  services$: Observable<Service[]>;
  servicesListOptions: {} = {
    tools: {
      add: true
    },
    price: false,
    tags: false
  };

  // Analyzes
  analyzes$: Observable<Analyzes[]>;
  analyzesListOptions = {
    tools: {
      check: true
    },
    view: {
      price: true
    }
  };

  // All
  checked: {
    services: Service[];
    analyzes: Analyzes[];
    summ: number;
  } = {
    services: [],
    analyzes: [],
    summ: 0
  };

  constructor(
    private servicesService: ServicesService,
    private pagination: PaginationService,
    private analyzesService: AnalyzesService,
    private _tools: ToolsService
    ) { }

  ngOnInit() {
    this.reloadServices(this.servicesFilter);
    this.initProfiles();
    this.initAnalyzes();
  }

  reloadServices(params?: any) {
    this.services$ = this.servicesService.get(params);

    this.services$.subscribe();
  }

  initAnalyzes() {
    this.pagination.setParams({
      nPerPage: 10
    }, 'analyzes-check');
    this.reloadAnalyzes();
  }

  reloadAnalyzes(params?: any) {
    params = params !== undefined ? params : this.pagination.getParams('analyzes-check');

    this.analyzes$ = this.analyzesService.get(params);
    const analyzesCount$ = this.analyzesService.getCount(params);

    const $combined = Observable.concat(
      this.analyzes$,
      analyzesCount$
    );

    $combined
      .subscribe(
        result => {
          if(typeof result === 'number') {
            this.pagination.setParams({
              countItems: result
            }, 'analyzes-check');
            this.pagination.getPager('analyzes-check');
          }
        }
      );
  }

  checkSelectedServices(service?: Service) {
    this.clearChecked();

    this.servicesList.getSelectedItems().forEach( (item: Service) => {
      this.checked.summ += item.price;
      this.checked.services.push(new Service(item));
    });

    this.analyzesList.getSelectedItems().forEach( (item: Analyzes) => {
      this.checked.summ += item.price;
      this.checked.analyzes.push(new Analyzes(item));
    });
  }

  unSelectAnalyzes(item: Analyzes) {
    this.analyzesList.unSelectItem(item);
    this.checkSelectedServices();
  }

  unSelectServices(item: Service) {
    this.servicesList.unSelectItem(item);
    this.checkSelectedServices();
  }

  clearChecked() {
    this.checked = {
      services: [],
      analyzes: [],
      summ: 0
    };
  }

  clearCheckedGlobal() {
    this.clearChecked();
    this.analyzesList.clearSelected();
    this.servicesList.clearSelected();
  }

  changePageAnalyzes(params) {
    this.reloadAnalyzes(params);
  }

  onSearchAnalyzes(text: string) {
    let params = this.pagination.getParams('analyzes-check');
    params.currentPage = 1;
    params.text = text;

    this.reloadAnalyzes(params);
  }

  onSearchServices(text: string) {
    this.servicesFilter.search = text;

    this.reloadServices(this.servicesFilter);
  }

  getListCheckedServices() {
    var list = [];
    var added = {};

    this.checked.services.forEach((service) => {
      if(!!added[service._id]) {
        list.find((found, index) => {
          if(found.service._id == service._id) {
            list[index].count = list[index].count + 1;
            return true;
          }
        });
      }
      else {
        added[service._id] = true;
        list.push({count: 1, service: service});
      }
    });

    return list;
  }

  getCountServiceById(id) {
    var count = 0;
    this.checked.services.forEach((service) => {
      if(id == service._id) {
        count = count + 1;
      }
    });

    return count;
  }

  getCheckedServices() {
    return this.checked.services;
  }

  getCheckedAnalyzes() {
    return this.checked.analyzes;
  }

  setServicesFilterBySpecialists(specialists) {
    this.servicesFilter.tags = [];
    this.servicesFilter.variant = 'default';
    this.clearCheckedGlobal();
    
    if(specialists && specialists.length > 0) {
      this.servicesFilter.tags = [];
      specialists.forEach((spec) => {
        this.servicesFilter.tags = this.servicesFilter.tags.concat(spec.tags);
      });
      
      this.servicesFilter.variant = (specialists.length > 1) ? 'default' : specialists[0].doctorate;
    }
  }

  initProfiles() {
    this.profiles = [
      { name: " .", search: "02-029, 06-015, 07-049, 07-100, 07-025, 07-101, 03-003, 03-015, 03-010, 02-006"},
      { name: "", search: "02-003, 12-001" }
    ];
  }

  checkProfile(item) {
    this.onSearchAnalyzes(item.search);
  }

  resetAnalyzes() {
    this.pagination.setParams({currentPage: 1, text: ""}, 'analyzes-check');
    this.reloadAnalyzes();
  }

}



