import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import { ToolsService } from '../../shared/tools.service';
import { AuthenticationService } from '../../+users/_services/authentication.service';

import { PaymentsService } from '../payments.service';
import { Payment } from '../payment';

@Component({
  selector: 'app-payments',
  templateUrl: './payments.component.html',
  styleUrls: ['./payments.component.css']
})
export class PaymentsComponent implements OnInit, OnDestroy {
  payments$: Observable<Payment[]>;
  showTools: any;

  currentDate: Date;

  constructor(
    private _ps: PaymentsService,
    private _tools: ToolsService,
    private _router: Router,
    private _auth: AuthenticationService
  ) {
    this.initDay();
  }

  ngOnInit() {
    this._reloadPaymentByDate();
    this.getAllowedTools();
    
    this._ps.getChangePaymentsEvents().subscribe((result: any) => {
      this.actionForSocketEvents(result);
      this._reloadPaymentByDate();
    });
  }

  initDay() {
    let today = new Date();
    today.setHours(0, 0, 0, 0);

    this.currentDate = today;
  }

  nextDay() {
    let nextDay = new Date();
    nextDay.setDate(this.currentDate.getDate() + 1);

    this.currentDate = nextDay;
    this._reloadPaymentByDate();
  }

  prevDay() {
    let prevDay = new Date();
    prevDay.setDate(this.currentDate.getDate() - 1);

    this.currentDate = prevDay;
    this._reloadPaymentByDate();
  }

  _reloadPaymentByDate() {
    this.reloadPaymnet({ date_from: this.currentDate, date_to: new Date().setDate(this.currentDate.getDate() + 1) });
  }

  reloadPaymnet(params?: any) {
    this.payments$ = this._ps.get(params);

    this.payments$.subscribe();
  }

  onPay(_id) {
    this._router.navigate(['payments/pay/'+_id]);
  }

  onEdit(_id) {
    this._router.navigate(['payments/pay/create/'+_id]);
  }

  onDelete(_id) {
    this._ps.delete(_id)
      .subscribe((result) => {
        if(result.status == 'ok') {
          this._tools.getToast().toast(" ");
          this.reloadPaymnet();
        }
      });
  }

  private actionForSocketEvents(payment) {
    if(payment.status == 'open') {
      this._tools.getToast().toast("    ");
    } else if(payment.status == 'collected') {
      this._tools.getToast().toast("   ");
    } else {
      this._tools.getToast().toast("  !");
    }
  }

  private getAllowedTools() {
    this.showTools = {
      reserve: this._auth.checkRule("payments.reserve"),
      pay: this._auth.checkRule("payments.pay"),
      add: this._auth.checkRule("payments.add"),
      create: this._auth.checkRule("payments.create"),
      update: this._auth.checkRule("payments.update"),
      delete: this._auth.checkRule("payments.delete")
    }
  }

  ngOnDestroy() {
    this._ps.leaveSocket();
  }

}



