import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { PersonsWidgetModule } from '../+persons/_widgets/persons.widget';
import { ServicesWidgetModule } from '../+services/_widgets/services.widget';
import { SpecialistWidgetModule } from '../+specialists/_widgets/specialists.widget';
import { AnalyzesWidgetModule } from '../+analyzes/_widgets/analyzes.widget';
import { UsersWidgetModule } from '../+users/_widgets/users.widget';

import { PaymentsListComponent } from './payments-list/payments-list.component';
import { PaymentUpdateComponent } from './payment-update/payment-update.component';
import { PaymentsComponent } from './payments/payments.component';
import { PaymentFormComponent } from './payment-form/payment-form.component';
import { PaymentAddComponent } from './payment-add/payment-add.component';

import { PaymentsService } from './payments.service';
import { PaymentFormServicesSelectComponent } from './payment-form-services-select/payment-form-services-select.component';
import { PaymentReserveComponent } from './payment-reserve/payment-reserve.component';
import { PaymentPayComponent } from './payment-pay/payment-pay.component';
import { PaymentCreateComponent } from './payment-create/payment-create.component';

const PAYMENTS_ROUTES: Routes = [
  { path: '', component: PaymentsComponent },
  { path: 'pay/add', component: PaymentAddComponent },
  { path: 'pay/create/:id', component: PaymentCreateComponent },
  { path: 'pay/create', component: PaymentCreateComponent },
  { path: 'pay/reserve', component: PaymentReserveComponent },
  { path: 'pay/:id', component: PaymentPayComponent },
  { path: 'update/:id', component: PaymentUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(PAYMENTS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule,
    PersonsWidgetModule,
    ServicesWidgetModule,
    SpecialistWidgetModule,
    AnalyzesWidgetModule,
    UsersWidgetModule
  ],
  declarations: [PaymentsListComponent, PaymentAddComponent, PaymentUpdateComponent, PaymentsComponent, PaymentFormComponent, PaymentFormServicesSelectComponent, PaymentReserveComponent, PaymentPayComponent, PaymentCreateComponent],
  providers: [PaymentsService],
  exports: [PaymentsComponent]
})
export class PaymentsModule { }



